'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import Link from 'next/link';
import Image from 'next/image';
import { toast } from 'sonner';
import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';
import { 
    Calendar, Clock, MapPin, CreditCard, ArrowLeft, Upload, XCircle, CheckCircle2, AlertCircle, Ban, Loader2, ImageIcon, History
} from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
    Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogFooter
} from '@/components/ui/dialog';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

import { Booking } from '@/types';
import useBookingsSync from '@/hooks/useBookingsSync';

export default function BookingHistoryPage() {
    const { bookings, loading, refresh } = useBookingsSync();

    // State Upload
    const [selectedBooking, setSelectedBooking] = useState<Booking | null>(null);
    const [isUploadOpen, setIsUploadOpen] = useState(false);
    const [fileBukti, setFileBukti] = useState<File | null>(null);
    const [isUploading, setIsUploading] = useState(false);
    const formatRupiah = (num: number | string) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));

    const formatDate = (dateString: string) => 
        format(new Date(dateString), 'EEEE, dd MMMM yyyy', { locale: localeId });

    const getStatusBadge = (statusId: number) => {
        switch(statusId) {
            case 1: return <Badge className="bg-yellow-100 text-yellow-800 hover:bg-yellow-100 border-yellow-200"><AlertCircle className="w-3 h-3 mr-1"/> Menunggu Pembayaran</Badge>;
            case 2: return <Badge className="bg-blue-100 text-blue-800 hover:bg-blue-100 border-blue-200"><Clock className="w-3 h-3 mr-1"/> Menunggu Konfirmasi</Badge>;
            case 3: return <Badge className="bg-green-100 text-green-800 hover:bg-green-100 border-green-200"><CheckCircle2 className="w-3 h-3 mr-1"/> Terkonfirmasi</Badge>;
            case 4: return <Badge className="bg-red-500 text-white hover:bg-red-700 border-red-200"><Ban className="w-3 h-3 mr-1"/> Dibatalkan</Badge>;
            default: return <Badge variant="outline">Unknown</Badge>;
        }
    };

    const handleUpload = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!selectedBooking || !fileBukti) return;

        setIsUploading(true);
        try {
            const formData = new FormData();
            formData.append('bukti_pembayaran', fileBukti);
            
            await axios.post(`/api/bookings/${selectedBooking.id}/payment`, formData);
            
            toast.success("Bukti pembayaran berhasil diupload!");
            setIsUploadOpen(false);
            setFileBukti(null);
            refresh();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal upload bukti.");
        } finally {
            setIsUploading(false);
        }
    };

    const handleCancel = async (id: number) => {
        if(!confirm("Yakin ingin membatalkan booking ini?")) return;
        try {
            await axios.patch(`/api/bookings/${id}/cancel`);
            toast.success("Booking dibatalkan.");
            refresh();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal membatalkan.");
        }
    };


    const activeBookings = bookings.filter(b => [1, 2, 3].includes(b.status_booking_id));
    const riwayatBookings = bookings.filter(b => [4, 5, 6].includes(b.status_booking_id));

    return (
        <div className="min-h-screen bg-[#f8f9fa] pt-24 pb-12 px-4 md:px-8 font-sans">
            <div className="max-w-4xl mx-auto">
                
                <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-8">
                    <div>
                        <Link href="/pelanggan/home" className="text-gray-500 text-sm hover:text-[#D93F21] mb-1 flex items-center">
                            <ArrowLeft className="w-4 h-4 mr-1" /> Kembali ke Home
                        </Link>
                        <h1 className="text-3xl font-bold text-gray-900">Riwayat Booking</h1>
                        <p className="text-gray-500 text-sm mt-1">Kelola jadwal main dan status pembayaran Anda.</p>
                    </div>
                    <Link href="/pelanggan/booking/create">
                        <Button className="bg-[#D93F21] hover:bg-[#b9351b] rounded-full shadow-lg font-bold">
                            + Booking Baru
                        </Button>
                    </Link>
                </div>

                {/* Tabs */}
                <Tabs defaultValue="active" className="w-full">
                    <TabsList className="grid w-full grid-cols-2 mb-8 bg-white p-1 rounded-xl border border-gray-200 h-auto shadow-sm">
                        <TabsTrigger value="active" className="rounded-lg py-3 font-medium data-[state=active]:bg-[#D93F21] data-[state=active]:text-white transition-all">
                            Booking Aktif ({activeBookings.length})
                        </TabsTrigger>
                        <TabsTrigger value="riwayat" className="rounded-lg py-3 font-medium data-[state=active]:bg-gray-800 data-[state=active]:text-white transition-all">
                            Riwayat Selesai / Batal
                        </TabsTrigger>
                    </TabsList>

                    <TabsContent value="active" className="space-y-4">
                        {loading ? <div className="text-center py-12"><Loader2 className="w-8 h-8 animate-spin mx-auto text-gray-400"/></div> : 
                         activeBookings.length === 0 ? (
                            <div className="text-center py-16 bg-white rounded-2xl border border-dashed border-gray-300">
                                <Calendar className="w-12 h-12 text-gray-300 mx-auto mb-3" />
                                <p className="text-gray-500 font-medium">Belum ada booking aktif.</p>
                                <p className="text-sm text-gray-400 mt-1">Jadwal main basketmu akan muncul di sini.</p>
                            </div>
                         ) : (
                            activeBookings.map(item => (
                                <BookingCard 
                                    key={item.id} 
                                    item={item} 
                                    formatDate={formatDate} 
                                    formatRupiah={formatRupiah} 
                                    getStatusBadge={getStatusBadge}
                                    onUpload={() => { setSelectedBooking(item); setIsUploadOpen(true); }}
                                    onCancel={() => handleCancel(item.id)}
                                    isRiwayat={false} 
                                />
                            ))
                        )}
                    </TabsContent>

        
                    <TabsContent value="riwayat" className="space-y-4">
                         {loading ? <div className="text-center py-12"><Loader2 className="w-8 h-8 animate-spin mx-auto text-gray-400"/></div> : 
                         riwayatBookings.length === 0 ? (
                            <div className="text-center py-16 bg-white rounded-2xl border border-dashed border-gray-300">
                                <History className="w-12 h-12 text-gray-300 mx-auto mb-3" />
                                <p className="text-gray-500 font-medium">Belum ada riwayat booking lama.</p>
                            </div>
                         ) : (
                            riwayatBookings.map(item => (
                                <BookingCard 
                                    key={item.id} 
                                    item={item} 
                                    formatDate={formatDate} 
                                    formatRupiah={formatRupiah} 
                                    getStatusBadge={getStatusBadge}
                                    isRiwayat={true} 
                                />
                            ))
                        )}
                    </TabsContent>
                </Tabs>

                <Dialog open={isUploadOpen} onOpenChange={setIsUploadOpen}>
                    <DialogContent className="sm:max-w-md">
                        <DialogHeader>
                            <DialogTitle>Upload Bukti Pembayaran</DialogTitle>
                        </DialogHeader>
                        <form onSubmit={handleUpload} className="space-y-4 py-4">
                            <div className="p-4 bg-blue-50 border border-blue-100 rounded-lg">
                                <div className="flex justify-between items-center mb-2">
                                    <p className="text-sm text-blue-700 font-medium">Kode Booking:</p>
                                    <p className="text-sm font-mono font-bold text-blue-900">{selectedBooking?.kode_booking}</p>
                                </div>
                                <div className="flex justify-between items-center">
                                    <p className="text-sm text-blue-700 font-medium">Total Tagihan:</p>
                                    <p className="text-xl font-bold text-blue-900">{selectedBooking ? formatRupiah(Number(selectedBooking.total_harga)) : 0}</p>
                                </div>
                            </div>
                            
                            <div className="space-y-2">
                                <Label>File Bukti Transfer (JPG/PNG)</Label>
                                <div className="relative">
                                    <Input 
                                        type="file" 
                                        accept="image/*" 
                                        onChange={(e) => setFileBukti(e.target.files?.[0] || null)}
                                        required
                                        className="pl-10 py-2 h-12 cursor-pointer file:mr-4 file:py-1 file:px-4 file:rounded-full file:border-0 file:text-xs file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100"
                                    />
                                    <ImageIcon className="absolute left-3 top-3.5 w-5 h-5 text-gray-400" />
                                </div>
                            </div>

                            <DialogFooter>
                                <Button type="submit" disabled={isUploading} className="w-full bg-[#D93F21] hover:bg-[#b9351b]">
                                    {isUploading ? <Loader2 className="animate-spin mr-2" /> : <Upload className="w-4 h-4 mr-2" />}
                                    Kirim Bukti
                                </Button>
                            </DialogFooter>
                        </form>
                    </DialogContent>
                </Dialog>

            </div>
        </div>
    );
}


function BookingCard({ item, formatDate, formatRupiah, getStatusBadge, onUpload, onCancel, isRiwayat }: any) {
    return (
        <Card className={`overflow-hidden border-l-4 ${isRiwayat ? 'border-l-gray-300 opacity-80' : 'border-l-[#D93F21]'} shadow-sm hover:shadow-md transition-all duration-300 bg-white`}>
            <CardContent className="p-6">
                <div className="flex flex-col md:flex-row justify-between gap-6">
                    
                    <div className="flex-1 space-y-3">
                        <div className="flex items-center justify-between md:justify-start gap-3 mb-1">
                            <span className="font-mono text-xs font-bold text-gray-600 bg-gray-100 px-2 py-1 rounded border border-gray-200">
                                {item.kode_booking}
                            </span>
                            {getStatusBadge(item.status_booking_id)}
                        </div>
                        
                        <div>
                            <h3 className="font-bold text-lg text-gray-900 flex items-center gap-2">
                                <MapPin className="w-4 h-4 text-[#D93F21]" /> 
                                {item.lapangan?.nama_lapangan}
                            </h3>
                            <div className="text-sm text-gray-600 mt-2 space-y-1.5">
                                <p className="flex items-center gap-2">
                                    <Calendar className="w-4 h-4 text-gray-400" /> 
                                    {formatDate(item.tanggal_booking)}
                                </p>
                                <p className="flex items-center gap-2">
                                    <Clock className="w-4 h-4 text-gray-400" /> 
                                    {item.jam_mulai} - {item.jam_selesai} ({item.durasi_jam} Jam)
                                </p>
                            </div>
                        </div>
                        
                        {item.acara && (
                            <div className="text-xs text-gray-500 bg-gray-50 p-2 rounded-md inline-block border border-gray-100">
                                Club: <span className="font-bold text-gray-700">{item.acara}</span>
                            </div>
                        )}
                    </div>

                    <div className="flex flex-col justify-between items-end min-w-[150px] border-t md:border-t-0 md:border-l border-gray-100 pt-4 md:pt-0 md:pl-6 mt-2 md:mt-0">
                        <div className="text-right">
                            <p className="text-xs text-gray-500 mb-1">Total Tagihan</p>
                            <p className="text-xl font-extrabold text-gray-900">{formatRupiah(item.total_harga)}</p>
                            <div className="mt-1 text-xs text-gray-500 flex items-center justify-end gap-1">
                                <CreditCard className="w-3 h-3" /> {item.payment_method?.nama_metode}
                            </div>
                        </div>

                        {!isRiwayat && (
                            <div className="flex flex-col gap-2 w-full mt-6">
                                {item.status_booking_id === 1 && (
                                    <>
                                        <Button size="sm" className="w-full bg-[#D93F21] hover:bg-[#b9351b] shadow-sm" onClick={onUpload}>
                                            <Upload className="w-3.5 h-3.5 mr-2" /> Upload Bukti
                                        </Button>
                                        <Button size="sm" variant="outline" className="w-full text-red-600 border-red-200 hover:bg-red-50" onClick={onCancel}>
                                            Batalkan
                                        </Button>
                                    </>
                                )}
                                {item.status_booking_id === 2 && (
                                    <Button size="sm" variant="secondary" className="w-full cursor-default opacity-80 bg-blue-50 text-blue-700 hover:bg-blue-50 border border-blue-100">
                                        <Loader2 className="w-3.5 h-3.5 mr-2 animate-spin" /> Sedang Diverifikasi
                                    </Button>
                                )}
                                {item.status_booking_id === 3 && ( 
                                    <Button size="sm" variant="outline" className="w-full border-green-200 text-green-700 bg-green-50 cursor-default hover:bg-green-50">
                                        <CheckCircle2 className="w-3.5 h-3.5 mr-2" /> Siap Main! üèÄ
                                    </Button>
                                )}
                            </div>
                        )}
                    </div>
                </div>
            </CardContent>
        </Card>
    );
}